@import url('https://fonts.googleapis.com/css2?family=Recursive:wght@300;400&display=swap');

.SiteLoaderDigit {
    width: 0.6em;
    margin: 0;
}

@keyframes rising-sun {
    from {
        background-color: red;
    }

    20% {
        background-color: rgb(255, 136, 0);
    }

    to {
        background-color: rgb(255, 255, 255);
    }
}

.SiteLoader {
    z-index: 100;

    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    background-color: black;
    font-size: 2vh;

    .background-container {
        position: relative;
        min-width: 100vw;
        min-height: 100%;

        --mountain-color-0: #083327;
        --mountain-color-1: #0a7e5b;
        --mountain-color-bg: #00bc72;
        --tree-color-bg: #00986a;

        .sky {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            opacity: 0;
            mix-blend-mode: lighten;
            transition: 0.5s linear opacity;

            &.sky-0 {
                background: #101116;
            }

            &.sky-1 {
                background: linear-gradient(0deg, rgba(209,0,0,1) 0%, rgba(215,23,9,1) 25%, rgba(255,189,71,1) 30%, rgba(235,173,111,1) 40%, rgba(126,152,187,1) 60%, rgba(0,2,75,1) 100%);
            }

            &.sky-2 {
                background: linear-gradient(0deg, rgba(209,0,0,1) 0%, rgba(219,154,54,1) 25%, rgba(217,170,123,1) 50%, rgba(147,171,204,1) 66%, rgba(73,78,255,1) 100%);
            }

            &.sky-3 {
                background: linear-gradient(0deg, rgba(159,205,235,1) 0%, rgba(137,172,221,1) 54%, rgba(82,125,255,1) 100%);
            }
        }

        .landscape {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            width: 100vw;
            height: 100%;
        }

        .rising-sun {
            position: absolute;
            bottom: 15vh;
            left: 60vw;
            display: block;
            width: 10em;
            height: 10em;
            border-radius: 50%;
            background-color: var(--sun-color);

            transform-origin: -30vw 0;
            transition: transform 0.5s linear, background-color 1s linear;

            &::after {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                border-radius: 50%;
                box-shadow: 1px 1px var(--sun-shine-radius, 50px) 1px var(--sun-color), 0px 0px 100px 1px var(--sun-color);
                transition: 0.5s linear all;
            }
        }

    }

    .progress-text {
        user-select: none;
        position: absolute;
        left: 0.5em;
        bottom: 0.5em;
        display: flex;
        font-size: 4em;
        color: white;
        transition: 0.5s ease-out bottom;

        font-family: 'Recursive', sans-serif;
        font-weight: 300;

        .percent-sign {
            margin-left: 0.3em;
            margin: 0;
        }
    }

    .progress-cover {
        position: absolute;
        bottom: 0;
        display: flex;
        width: 100%;
        height: 3em;
        background-color: black;
    }

    .whiteout {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: white;
    }
}