
.TeamCard{
    *{
        box-sizing: border-box;
    }

    $size: 1.1;
    $protrusion : 40%;
    $gradient:linear-gradient(135deg, #21D4FD , #B721FF);// linear-gradient(135deg, #F84949 , #AC2A92);

    width: 28rem*$size ;
    background-color: #38c6f1;
    margin: 1rem;
    margin-top: $protrusion/10*3*$size ;
    transition-duration: .5s;
    box-shadow: 2px 3px 1rem rgba(0, 0, 0, 0.63);
    pointer-events: auto;

    &:hover{
        transform: scale(1.05) translateY(-1rem);
        box-shadow: 0px 5px 2rem rgba(0, 0, 0, 0.753);
    }

    &__img-block{
        width: 100%;
        height: 15rem*$size ;
        position: relative;
        background: $gradient;
    }
    &__img{
        position: absolute;
        width: 100%+$protrusion;
        height: 100%+$protrusion;
        object-fit: scale-down;
        top:-$protrusion;
        left: -$protrusion/2;
    }
    &__img-background{
        width: 100%;
        height: 100%;
        object-fit: scale-down;
    }

    &__heading{
        position: absolute;
        top: 50%;
        display: block;
        width: 100%;
        font-size: 2.9rem*$size ;
        text-transform: uppercase;
        text-align: center;
        margin: 0rem;
        text-shadow: 2px 2px 1rem black;
    }

    &__content{
        background-color: #ffffff;
        height: 13rem*$size ;
        padding: 1rem;
        color:#000000;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
    }
    
    &__description{
        display: block;
        margin: 0rem;
        width: 100%;
        font-size: 1.6rem*$size ;
        opacity: .6;
        text-align: center;
    }
    &__stats-container{
        width: 100%;
        height: 8rem*$size ;
        background: $gradient;
        display: flex;

    }
    &__stat{
        width: 33%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        padding: 2px;
        line-height: 3rem*$size ;
        color: white;
        &--big{
            width: 66%;
        }
        &:not(:first-child) {
            border-left: 1px solid #ffffff55;
        }
    }
    
    &__value{
        width: 100%;
        font-size: 2.2rem*$size ;
        text-align: center;
        &--big{
            font-size: 2.6rem*$size ;
        }
    }
    &__name{
        width: 100%;
        font-size: 1.6rem*$size ;
        opacity: .7;
        text-align: center;
        //text-transform: uppercase;
    }
}