@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300;400&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

#threejs-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}
.Timeline__label{color:white;font-weight:600;width:50rem;text-align:center;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);pointer-events:none !important;z-index:0;text-shadow:1px 1px 10px rgba(0,0,0,0.568)}.Timeline__label-date{font-size:8rem;margin:1rem;pointer-events:none}.Timeline__label-header{font-size:4rem;margin:.5rem}.Timeline__label-description{font-size:2.4rem;margin:.5rem}html{font-size:62.5%;box-sizing:border-box}@media only screen and (max-width: 1800px){html{font-size:50%}}body{background-color:black;font-size:1.6rem}.App{min-height:100vh;--background-color: #19284d;--background-color-light: #203662;--background-color-lighter: #2c5384}.App.nzn{--background-color: #6b0000;--background-color-light: #ce3c10;--background-color-lighter: #ffb62f}.App.contact{--background-color: #000000;--background-color-light: #222222;--background-color-lighter: #313131}.App .App__main{position:relative;min-height:100vh;overflow-x:hidden;overflow-y:hidden}.App .App__main.App__main--page-switch-left{--page-switch-shove: -50px}.App .App__main .App__page-content{width:100vw}.App .App__main .App__page-content.router-fade-enter{position:fixed;top:0;left:0;right:0;opacity:0.0;-webkit-transform:translateX(50px);transform:translateX(50px);-webkit-transform:translateX(var(--page-switch-shove, 50px));transform:translateX(var(--page-switch-shove, 50px))}.App .App__main .App__page-content.router-fade-enter.router-fade-enter-active{-webkit-transform:translateX(0px);transform:translateX(0px);opacity:1;transition:opacity 150ms ease-in 150ms, -webkit-transform 150ms ease-out 150ms;transition:opacity 150ms ease-in 150ms, transform 150ms ease-out 150ms;transition:opacity 150ms ease-in 150ms, transform 150ms ease-out 150ms, -webkit-transform 150ms ease-out 150ms}.App .App__main .App__page-content.router-fade-exit{opacity:1}.App .App__main .App__page-content.router-fade-exit.router-fade-exit-active{opacity:0.01;transition:opacity 150ms ease-in, -webkit-transform 150ms ease-out;transition:opacity 150ms ease-in, transform 150ms ease-out;transition:opacity 150ms ease-in, transform 150ms ease-out, -webkit-transform 150ms ease-out}.App:after{-webkit-animation:grain 8s steps(10) infinite;animation:grain 8s steps(10) infinite;background-image:url(/agh-solar-plane/static/media/img-noise-512x512.fc205488.png);content:"";height:300%;left:-50%;opacity:0.3;position:fixed;z-index:10000;top:-100%;width:300%;mix-blend-mode:darken;pointer-events:none}@-webkit-keyframes grain{0%,100%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}10%{-webkit-transform:translate(-5%, -10%);transform:translate(-5%, -10%)}20%{-webkit-transform:translate(-15%, 5%);transform:translate(-15%, 5%)}30%{-webkit-transform:translate(7%, -25%);transform:translate(7%, -25%)}40%{-webkit-transform:translate(-5%, 25%);transform:translate(-5%, 25%)}50%{-webkit-transform:translate(-15%, 10%);transform:translate(-15%, 10%)}60%{-webkit-transform:translate(15%, 0%);transform:translate(15%, 0%)}70%{-webkit-transform:translate(0%, 15%);transform:translate(0%, 15%)}80%{-webkit-transform:translate(3%, 35%);transform:translate(3%, 35%)}90%{-webkit-transform:translate(-10%, 10%);transform:translate(-10%, 10%)}}@keyframes grain{0%,100%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}10%{-webkit-transform:translate(-5%, -10%);transform:translate(-5%, -10%)}20%{-webkit-transform:translate(-15%, 5%);transform:translate(-15%, 5%)}30%{-webkit-transform:translate(7%, -25%);transform:translate(7%, -25%)}40%{-webkit-transform:translate(-5%, 25%);transform:translate(-5%, 25%)}50%{-webkit-transform:translate(-15%, 10%);transform:translate(-15%, 10%)}60%{-webkit-transform:translate(15%, 0%);transform:translate(15%, 0%)}70%{-webkit-transform:translate(0%, 15%);transform:translate(0%, 15%)}80%{-webkit-transform:translate(3%, 35%);transform:translate(3%, 35%)}90%{-webkit-transform:translate(-10%, 10%);transform:translate(-10%, 10%)}}

.Header{z-index:1;position:fixed;top:0;display:flex;box-sizing:border-box;width:100%;justify-content:flex-end;align-items:center}.Header .actions{margin:3rem;padding:3rem;transition-duration:0.6s}.Header .actions a{position:relative;padding:0.2rem 0.7em;margin:0 0.7rem;color:white;border:0;font-family:'Noto Sans', sans-serif;font-size:2rem;text-decoration:none;transition:0.3s ease-in-out color}.Header .actions a::after{content:"";position:absolute;top:100%;left:50%;-webkit-transform:translateX(-50%) translateY(0px) scaleX(0);transform:translateX(-50%) translateY(0px) scaleX(0);width:80%;height:3px;background:currentColor;transition:0.3s ease-in-out background-color, 0.3s ease-out transform}.Header .actions a.active::after{-webkit-transform:translateX(-50%) translateY(0px) scaleX(1);transform:translateX(-50%) translateY(0px) scaleX(1)}.Header .actions a:hover::after{-webkit-transform:translateX(-50%) translateY(-5px) scaleX(1);transform:translateX(-50%) translateY(-5px) scaleX(1)}.Header .actions--scroll{background-color:#ffffff;-webkit-transform:translateY(0);transform:translateY(0)}.Header .actions--scroll.hidden{-webkit-transform:translateY(-200%);transform:translateY(-200%)}.Header .actions--scroll a{color:black}

.Banner{position:relative;--overlay-color: rgba(255, 253, 241, 0.9);width:100%;height:100vh}.Banner__background{width:calc(100% + 100px);height:100%;margin-left:-50px;margin-right:-50px;overflow:hidden}.Banner__background-content{width:100%;height:100%;object-fit:cover}.Banner__background-overlay{background-color:rgba(0,0,0,0.2);box-shadow:inset 0 0 100px black;width:calc(100% + 100px);height:100%;position:absolute;left:0;top:0}.Banner__overlay{width:100%;height:100vh;position:absolute;left:0;top:0;color:var(--overlay-color);display:flex;align-items:center}.Banner__overlay-text{position:absolute;box-sizing:border-box;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#ffffff;width:100rem;max-width:80%;padding:3rem;text-align:left;font-size:6rem;font-weight:600;letter-spacing:.5rem;text-align:center;opacity:.9;text-shadow:.1rem .1rem 3rem #000000;border-bottom:.5rem solid #ffffff}.Banner__overlay-content{width:100%;height:100%;object-fit:cover}.Banner__overlay-logo{height:12rem;position:absolute;left:4rem;top:4rem}.Banner__action{width:100%;height:20vh;position:absolute;left:0;bottom:0;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:2rem;font-weight:bold;color:white;overflow:hidden;text-transform:uppercase;opacity:.6}

.SideStoryPanel{display:flex;align-items:center;justify-content:flex-end;pointer-events:none;transition:0.2s ease-out opacity}.SideStoryPanel .SideStoryPanel--panel{width:50%}.SideStoryPanel .SideStoryPanel--panel .SideStoryPanel--title{display:block;color:white;font-size:7rem;margin:0;margin-bottom:3rem;-webkit-transform:translate(-2rem, 0);transform:translate(-2rem, 0);transition:0.2s ease-in-out transform}.SideStoryPanel .SideStoryPanel--panel .SideStoryPanel--description{color:white;font-size:3rem;margin-right:10rem;max-width:100rem}.SideStoryPanel.visible{opacity:1}.SideStoryPanel.visible .SideStoryPanel--title{-webkit-transform:translate(0, 0);transform:translate(0, 0)}

.Stat{width:18rem;height:15rem;padding:2rem .5rem;margin:1rem}.Stat__number{width:100%;text-align:center;font-size:4rem;font-weight:600}.Stat__name{width:100%;text-align:center;font-size:2.5rem;vertical-align:middle;margin-top:3rem;line-height:2.6rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}

.StatContainer{width:100%;display:flex;justify-content:space-between;flex-wrap:wrap;margin:4rem 0}

.TeamCard{width:30.8rem;background-color:#38c6f1;margin:1rem;margin-top:13.2%;transition-duration:.5s;box-shadow:2px 3px 1rem rgba(0,0,0,0.63);pointer-events:auto}.TeamCard *{box-sizing:border-box}.TeamCard:hover{-webkit-transform:scale(1.05) translateY(-1rem);transform:scale(1.05) translateY(-1rem);box-shadow:0px 5px 2rem rgba(0,0,0,0.753)}.TeamCard__img-block{width:100%;height:16.5rem;position:relative;background:linear-gradient(135deg, #21D4FD, #B721FF)}.TeamCard__img{position:absolute;width:140%;height:140%;object-fit:scale-down;top:-40%;left:-20%}.TeamCard__img-background{width:100%;height:100%;object-fit:scale-down}.TeamCard__heading{position:absolute;top:50%;display:block;width:100%;font-size:3.19rem;text-transform:uppercase;text-align:center;margin:0rem;text-shadow:2px 2px 1rem black}.TeamCard__content{background-color:#ffffff;height:14.3rem;padding:1rem;color:#000000;display:flex;justify-content:center;align-content:center;flex-wrap:wrap}.TeamCard__description{display:block;margin:0rem;width:100%;font-size:1.76rem;opacity:.6;text-align:center}.TeamCard__stats-container{width:100%;height:8.8rem;background:linear-gradient(135deg, #21D4FD, #B721FF);display:flex}.TeamCard__stat{width:33%;height:100%;display:flex;justify-content:center;align-content:center;flex-wrap:wrap;padding:2px;line-height:3.3rem;color:white}.TeamCard__stat--big{width:66%}.TeamCard__stat:not(:first-child){border-left:1px solid #ffffff55}.TeamCard__value{width:100%;font-size:2.42rem;text-align:center}.TeamCard__value--big{font-size:2.86rem}.TeamCard__name{width:100%;font-size:1.76rem;opacity:.7;text-align:center}

.TeamSection{position:relative;left:50%;width:50%;height:100%;max-width:100%;color:white}.TeamSection__header{font-size:4rem;text-transform:uppercase}.TeamSection__description{font-size:1.8rem;width:50rem}.TeamSection__container{width:100%;max-width:100rem;height:100%;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap}.TeamSection__pair{width:100%;max-width:100%;display:flex;justify-content:space-around;flex-wrap:wrap}

.SiteLoaderDigit{width:0.6em;margin:0}@-webkit-keyframes rising-sun{from{background-color:red}20%{background-color:#f80}to{background-color:#fff}}@keyframes rising-sun{from{background-color:red}20%{background-color:#f80}to{background-color:#fff}}.SiteLoader{z-index:100;position:fixed;top:0;right:0;bottom:0;left:0;display:flex;background-color:black;font-size:2vh}.SiteLoader .background-container{position:relative;min-width:100vw;min-height:100%;--mountain-color-0: #083327;--mountain-color-1: #0a7e5b;--mountain-color-bg: #00bc72;--tree-color-bg: #00986a}.SiteLoader .background-container .sky{position:absolute;top:0;right:0;bottom:0;left:0;opacity:0;mix-blend-mode:lighten;transition:0.5s linear opacity}.SiteLoader .background-container .sky.sky-0{background:#101116}.SiteLoader .background-container .sky.sky-1{background:linear-gradient(0deg, #d10000 0%, #d71709 25%, #ffbd47 30%, #ebad6f 40%, #7e98bb 60%, #00024b 100%)}.SiteLoader .background-container .sky.sky-2{background:linear-gradient(0deg, #d10000 0%, #db9a36 25%, #d9aa7b 50%, #93abcc 66%, #494eff 100%)}.SiteLoader .background-container .sky.sky-3{background:linear-gradient(0deg, #9fcdeb 0%, #89acdd 54%, #527dff 100%)}.SiteLoader .background-container .landscape{position:absolute;top:0;right:0;bottom:0;left:0;width:100vw;height:100%}.SiteLoader .background-container .rising-sun{position:absolute;bottom:15vh;left:60vw;display:block;width:10em;height:10em;border-radius:50%;background-color:var(--sun-color);-webkit-transform-origin:-30vw 0;transform-origin:-30vw 0;transition:background-color 1s linear, -webkit-transform 0.5s linear;transition:transform 0.5s linear, background-color 1s linear;transition:transform 0.5s linear, background-color 1s linear, -webkit-transform 0.5s linear}.SiteLoader .background-container .rising-sun::after{content:"";display:block;position:absolute;top:0;right:0;bottom:0;left:0;border-radius:50%;box-shadow:1px 1px 50px 1px var(--sun-color),0px 0px 100px 1px var(--sun-color);box-shadow:1px 1px var(--sun-shine-radius, 50px) 1px var(--sun-color),0px 0px 100px 1px var(--sun-color);transition:0.5s linear all}.SiteLoader .progress-text{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;position:absolute;left:0.5em;bottom:0.5em;display:flex;font-size:4em;color:white;transition:0.5s ease-out bottom;font-family:'Recursive', sans-serif;font-weight:300}.SiteLoader .progress-text .percent-sign{margin-left:0.3em;margin:0}.SiteLoader .progress-cover{position:absolute;bottom:0;display:flex;width:100%;height:3em;background-color:black}.SiteLoader .whiteout{position:absolute;top:0;right:0;bottom:0;left:0;background-color:white}

