.SideStoryPanel {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    pointer-events: none;
    transition: 0.2s ease-out opacity;
    
    .SideStoryPanel--panel {
        width: 50%;

        .SideStoryPanel--title {
            display: block;
            color: white;
            font-size: 7rem;
            margin: 0;
            margin-bottom: 3rem;

            transform: translate(-2rem, 0);
            transition: 0.2s ease-in-out transform;
        }

        .SideStoryPanel--description {
            color: white;
            font-size: 3rem;
            margin-right: 10rem;
            max-width: 100rem;
        }
    }

    &.visible {
        opacity: 1;
        
        .SideStoryPanel--title {
            transform: translate(0, 0);
        }
    }
}