@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');

.Header {    
    z-index: 1;
    position: fixed;
    top: 0;
    display: flex;
    box-sizing: border-box;
    width: 100%;
    justify-content: flex-end;
    align-items: center;        

    .actions {
        margin: 3rem;
        padding: 3rem;
        transition-duration: 0.6s;
      
        a {
            position: relative;
            padding: 0.2rem 0.7em;
            margin: 0 0.7rem;
            color: white;
            border: 0;
            font-family: 'Noto Sans', sans-serif;
            font-size: 2rem;
            text-decoration: none;
            transition: 0.3s ease-in-out color;
            
            &::after {
                content: "";
                position: absolute;
                top: 100%;
                left: 50%;
                transform: translateX(-50%) translateY(0px) scaleX(0);
                width: 80%;
                height: 3px;
                background: currentColor;
                transition: 0.3s ease-in-out background-color, 0.3s ease-out transform;
            }

            &.active::after {               
                transform: translateX(-50%) translateY(0px) scaleX(1);
            }

            &:hover::after {
                transform: translateX(-50%) translateY(-5px) scaleX(1);
            }
        }

        &--scroll{
            background-color: #ffffff;
            transform: translateY(0);
          
            &.hidden {
                transform: translateY(-200%);
            }
          
            & a {
                color: black;
            }
        }
    }
}