.Timeline{
    &__label{
        color: white;
        font-weight: 600;
        width: 50rem;
        text-align: center;
        transform: translate(-50%, -50%);
        pointer-events: none !important;
        z-index: 0;
        text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.568);
        &-date{
            font-size: 8rem;
            margin: 1rem;
            pointer-events: none;
        }
        &-header{
            font-size: 4rem;
            margin: .5rem;
        }
        &-description{
            font-size: 2.4rem;
            margin: .5rem;
        }
    }
    
}
