.Banner {
    position: relative;
    --overlay-color: rgba(255, 253, 241, 0.9);
    width: 100%;
    height: 100vh;

    &__background {
        width: calc(100% + 100px);
        height: 100%;
        margin-left: -50px;
        margin-right: -50px;
        overflow: hidden;

        &-content {
            width: 100%;
            height: 100%;
            object-fit: cover;
            // scale: 1.3;
        }

        &-overlay {
            background-color: rgba(0, 0, 0, 0.2);
            box-shadow: inset 0 0 100px black;
            width: calc(100% + 100px);
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
        }
    }

    &__overlay {
        width: 100%;
        height: 100vh;
        position: absolute;
        left: 0;
        top: 0;
        color: var(--overlay-color);
        display: flex;
        align-items: center;

        &-text {
            position: absolute;
            box-sizing: border-box;
            top:50%;
            left: 50%;
            transform: translate(-50%,-50%);
            color: #ffffff;
            width: 100rem;
            max-width: 80%;
            padding: 3rem;
            text-align: left;
            font-size: 6rem;
            font-weight: 600;
            letter-spacing: .5rem;
            text-align: center;
            opacity: .9;
            text-shadow: .1rem .1rem 3rem #000000;
            border-bottom: .5rem solid #ffffff;
        }

        &-content {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        
        &-logo{
            height: 12rem;
            position: absolute;
            left: 4rem;
            top: 4rem;
        }
    }

    &__action {
        width: 100%;
        height: 20vh;
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 2rem;
        font-weight: bold;
        color: white;
        overflow: hidden;        
        text-transform: uppercase;
        opacity: .6;
        // &-button {
        //     cursor: pointer;
        //     transform: translateY(0px) scaleX(1);
        //     transition-duration: .3s;
        //     animation: jello-vertical 5s both;
        //     animation-iteration-count: infinite;
        //     animation-delay: 2s;
        //     font-size: 3rem;

        //     &:hover {
        //         transform: translateY(10px) scale(1.2);
        //     }
        // }
    }
}


