.Stat{
    width: 18rem;
    height: 15rem;
    padding: 2rem .5rem;
    

    margin: 1rem;

    &__number{
        width: 100%;
        text-align: center;
        font-size: 4rem;
        font-weight: 600;
    }
    &__name{
        width: 100%;
        text-align: center;
        font-size: 2.5rem;
        vertical-align: middle;
        margin-top: 3rem;
        line-height: 2.6rem;
        transform: translateY(-50%);
    }
}